.header .header__burger {
  position: absolute;
  top: 22px;
  right: 23px;
  cursor: pointer;
  border: none;
  width: 24px;
  height: 16px;
  display: block;
  background-color: #ffffff;
  transition: 0.26s ease-in-out;
}
.header .header__burger::after, .header .header__burger::before {
  position: absolute;
  content: "";
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  top: 0;
  right: -4px;
}
.header .header__burger::after {
  top: 16px;
  box-shadow: 0 -8px 0 0 #000000;
}
@media (min-width: 768px) {
  .header .header__burger {
    display: none;
    position: relative;
  }
}

.header--nav-opened .header__burger::after {
  transform: rotate(-45deg);
  width: 29px;
  top: 8px;
  right: -3px;
  box-shadow: none;
  transition: 0.5s ease-in-out;
}
.header--nav-opened .header__burger::before {
  transform: rotate(45deg);
  width: 29px;
  top: 8px;
  right: -3px;
  transition: 0.5s ease-in-out;
}

.header__burger:hover::after {
  background-color: #68b738;
}

.header__burger:hover::before {
  background-color: #68b738;
}

.header__burger:active::after {
  background-color: rgba(104, 183, 56, 0.3);
  width: 28px;
}

.header__burger:active::before {
  background-color: rgba(104, 183, 56, 0.3);
  width: 28px;
}

